.card{
  width: 5vw;
  height: 7vh;
  border: #282c34 smooth 1px;
  color: white;
  background-color: rgba(255,255,255,0.2);
  font-size: 20px;
  font-family:monospace;
  padding: 0;
  border-radius: 0%;
  z-index: -1000;
  rotate: 0deg;

  transition: 1s;
  transition-timing-function: ease-in-out;
  transition-duration: 1s;

}

.active_card{
  width: 5rem;
  height: 5rem;
  border: #282c34 solid 1;
  font-size: 20px;
  font-family:monospace;
  background-color: rgba(178, 34, 34,0.9);
  color: white;
  margin: 10%;
  border-radius: 50%;
  z-index: -1000;
  rotate: 360deg;
  /*
  -webkit-animation: glow 5s ease-in-out infinite alternate;
  -moz-animation: glow 5s ease-in-out infinite alternate;
  animation: glow 5s ease-in-out infinite alternate;
  */
 

  transition: 1s;
  transition-timing-function:cubic-bezier(0, 0.5, 0.5, 1);
  transition-duration: 2s;
}
@-webkit-keyframes glow {
  from {
    box-shadow: 0 0 20px #ffffff0f, 0 0 20px #e6be8a4a, 0 0 30px #e6be8a4a, 0 0 40px #e6be8a4a, 0 0 50px #e6be8a4a, 0 0 60px #e6be8a4a, 0 0 70px #e6be8a4a;

  }
  to {
    box-shadow: 0 0 5px #ffffff49, 0 0 10px #ffffff51, 0 0 20px #ffdd005a, 0 0 30px #ffdd005a, 0 0 40px #ffdd005a, 0 0 50px #ffdd005a, 0 0 60px #ffdd005a;

  }
}


